import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import {
  Home,
  Gallery,
  Forms,
  Documentation,
  Email,
  EmptyPage,
  Error400,
  Error401,
  Error403,
  Error404,
  Error500,
  Error503,
  ForgotPassword,
  Login,
  Profile,
  Register,
  Cards,
  Charts,
  PricingCards,
  Blog,
  Icons,
  Maps,
  Store,
  DocsIntroduction,
  DocsAlerts,
  DocsAvatars,
  DocsButtons,
  DocsCards,
  DocsColors,
  DocsCharts,
  DocsFormComponents,
  DocsTags,
  DocsTypography
} from "./pages";

class App extends Component {
  render() {
    return (
      <Router>
        <React.Fragment>
          <Route exact path={process.env.PUBLIC_URL + "/"} component={Home} />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/gallery"}
            component={Gallery}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/form-elements"}
            component={Forms}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/email"}
            component={Email}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/empty"}
            component={EmptyPage}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/400"}
            component={Error400}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/401"}
            component={Error401}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/403"}
            component={Error403}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/404"}
            component={Error404}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/500"}
            component={Error500}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/503"}
            component={Error503}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/forgot-Password"}
            component={ForgotPassword}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/login"}
            component={Login}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/profile"}
            component={Profile}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/register"}
            component={Register}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/cards"}
            component={Cards}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/charts"}
            component={Charts}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/pricing-cards"}
            component={PricingCards}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/blog"}
            component={Blog}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/icons"}
            component={Icons}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/maps"}
            component={Maps}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/store"}
            component={Store}
          />

          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs"}
            component={DocsIntroduction}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/alerts"}
            component={DocsAlerts}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/avatars"}
            component={DocsAvatars}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/buttons"}
            component={DocsButtons}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/cards"}
            component={DocsCards}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/charts"}
            component={DocsCharts}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/colors"}
            component={DocsColors}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/form-components"}
            component={DocsFormComponents}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/tags"}
            component={DocsTags}
          />
          <Route
            exact
            path={process.env.PUBLIC_URL + "/docs/typography"}
            component={DocsTypography}
          />
        </React.Fragment>
      </Router>
    );
  }
}

export default App;



